    font-family: 'poppinsitalic';
    src: url(${___CSS_LOADER_URL_REPLACEMENT_4___}) format('woff2'),
         url(${___CSS_LOADER_URL_REPLACEMENT_5___}) format('woff');
    font-weight: normal;
    font-style: normal;
}

body {
    font-family: "poppinsregular", sans-serif;
    font-size: var(--16px);
    line-height: 1.6;
    letter-spacing: 0.12rem;
    background-color: var(--main-bg-color);
    color: white;
}

.container {
    padding: 0 10%;
}

.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 0;
}

.avatar-block {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 12px;
}

.avatar-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    overflow: hidden;
}

.avatar {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.header-date {
    font-size: var(--24px);
}

.search-block {
    display: flex;
    gap: 12px;
    align-items: center;
    justify-content: center;
}

.search-wrapper {
    position: relative;
}

.search-button {
    position: absolute;
    left: 6px;
    top: 12px;
}

svg {
    width: 20px;
    height: 20px;
    fill: white;
}

.search-input {
    background-color: var(--input-bg-color);
    padding: 12px 6px 12px 34px;
    border-radius: 20px;
}

.search-input:focus-visible {
    outline: 2px solid dodgerblue;
    outline-offset: 2px;
}

/* Custom Switches */

.switches input {
    appearance: none;
    position: relative;
    display: inline-block;
    background-color: var(--input-bg-color);
    height: 40px;
    width: 60px;
    vertical-align: middle;
    border-radius: 2rem;
    box-shadow: 0px 1px 3px #0003 inset;
    transition: background-color 0.25s linear;
}

.switches input::before {
    content: "";
    display: block;
    position: absolute;
    width: 28px;
    height: 28px;
    background-color: #fff;
    border-radius: 1.2rem;
    top: 6px;
    left: 0.2rem;
    box-shadow: 0px 1px 3px #0003;
    transition: transform 0.25s linear;
}

.switches input:checked:before {
    transform: translateX(26px);
}

.switches input:focus {
  outline-color: transparent;
}

.switches input:focus-visible {
    outline: 2px solid dodgerblue;
    outline-offset: 2px;
}

.switch-label {
    display: flex;
    align-items: center;
    gap: 8px
}

.chart {
    display: block;
    width: 20px;
}

.main {
    display: grid;
    grid-template-columns: 1fr;
    gap: 60px;
    grid-template-areas: 
    'main-data'
    'forecast';
}

.main-data {
    grid-area: main-data;
    background-color: #2e2e38;
    border-radius: 12px;
    padding: 40px;
    overflow: hidden;
}

.main-data-top {
    display: flex;
    justify-content: space-around;
    align-items: center;
    margin-bottom: 40px;
}

.main-data-bottom, .forecast-wrapper {
    width: 100%;
    overflow-x: scroll;
    padding-bottom: 14px;
}

.main-data-bottom::-webkit-scrollbar,
.forecast-wrapper::-webkit-scrollbar {
    height: 4px;
}

.main-data-bottom::-webkit-scrollbar-track,
.forecast-wrapper::-webkit-scrollbar-track {
    background: var(--hover-bg-color);
    border-radius: 20px;
}

.main-data-bottom::-webkit-scrollbar-thumb,
.forecast-wrapper::-webkit-scrollbar-thumb {
    background: var(--main-text-color);
    border-radius: 20px;
}

.hourly-weather,
.forecast-list {
    display: flex;
    gap: 40px;
}

.weather-img {
    width: 80px;
}

.city, .temperature, .humidity, .speed {
    font-size: var(--24px);
}

.country, .temperature-text, .humidity-text, .speed-text {
    font-size: var(--14px);
}

.hourly-image {
    width: 40px;
}

.hourly-item {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background-color: var(--card-bg-color);
    border-radius: 20px;
    padding: 12px;
    color: black;
}

.forecast {
    grid-area: forecast;
    border-radius: 12px;
    padding: 40px;
    overflow: hidden;
    background-color: #2e2e38;
}

.forecast-item {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background-color: rgba(30, 31, 36, 255);
    padding: 20px;
    border-radius: 20px;
    gap: 12px;
    flex-basis: 400px;
}

.item-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 6px;
